footer {
    color: rgb(224, 224, 224);
    margin-top: 5px;
    font-family: 'Kaushan Script', cursive;
    font-family: 'Stoke', serif;
 }
 .footerdiv{
     display: flex;
     background-color: rgba(29, 8, 15, 0.537);
     border-radius: 5px;
     border: 2px solid rgba(14, 10, 6, 0.438);
     justify-content: center;
     margin-top: 400px;
     margin-bottom: 0;
     width: 100%;
     position: relative; 
     bottom: 0;
     left: 0;
     text-align: center;
 }
 span{
     display: flex;
     justify-content: center;
 }
 
 .logosclass{
     width: 30px;
     height: 30px;
     margin-top: -8px;
     margin-bottom: 2px;
     padding-inline: 15px;
 }
 .samples{
     margin-top: 8px;
     font-size: 12px;
     color:rgb(151, 149, 149);
 }
 .samplesLink{
     color:  rgb(208, 204, 195);
 }
 
 @media screen and (max-width: 280px) {
     .samples{
         margin-top: 8px;
         font-size: 8px;
         color:rgb(151, 149, 149);
     }
   }